.error__container {

    padding: 10rem;
    background-color: rgb(180, 92, 33);
}

.error__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.error__content > h1 {
    color: white;
}

.error__content > iframe {
    padding-top: 4rem;
    width: 384px;
    height: 347px;
}


/* MEDIA QUERIES */
@media only screen and (max-width: 900px) {

    .error__container {

        padding: 7rem 1rem 7rem 1rem;
        background-color: rgb(180, 92, 33);
    }

    .error__content > h1 {

        font-size: 1.5rem;
    }

    .error__content > iframe {
        width: 290px;
        height: 300px;
    }
    
}
